.loader-chase {
  width: 100px;
  height: 100px;
  animation: loader-chase 2.5s linear infinite both;
  position: relative;
}

.loader-chase-dot {
  width: 100%;
  height: 100%;
  animation: loader-chase-dot 2s ease-in-out infinite both;
  position: absolute;
  top: 0;
  left: 0;
}

.loader-chase-dot:before {
  content: "";
  width: 25%;
  height: 25%;
  background-color: #fff;
  border-radius: 100%;
  animation: loader-chase-dot-before 2s ease-in-out infinite both;
  display: block;
}

.loader-chase-dot:nth-child(1) {
  animation-delay: -1.6s;
}

.loader-chase-dot:nth-child(2) {
  animation-delay: -1.4s;
}

.loader-chase-dot:nth-child(3) {
  animation-delay: -1.2s;
}

.loader-chase-dot:nth-child(4) {
  animation-delay: -1s;
}

.loader-chase-dot:nth-child(5) {
  animation-delay: -.8s;
}

.loader-chase-dot:nth-child(6) {
  animation-delay: -.6s;
}

.loader-chase-dot:nth-child(1):before {
  animation-delay: -1.6s;
}

.loader-chase-dot:nth-child(2):before {
  animation-delay: -1.4s;
}

.loader-chase-dot:nth-child(3):before {
  animation-delay: -1.2s;
}

.loader-chase-dot:nth-child(4):before {
  animation-delay: -1s;
}

.loader-chase-dot:nth-child(5):before {
  animation-delay: -.8s;
}

.loader-chase-dot:nth-child(6):before {
  animation-delay: -.6s;
}

@keyframes loader-chase {
  100% {
    transform: rotate(360deg);
  }
}

@keyframes loader-chase-dot {
  80%, 100% {
    transform: rotate(360deg);
  }
}

@keyframes loader-chase-dot-before {
  50% {
    transform: scale(.6);
  }

  100%, 0% {
    transform: scale(1);
  }
}

